footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: auto;
  padding: .5em;
}

footer a {
  color: #23538a;
  font-size: 1em;
}

#contact p {
  color: #23538a;
  margin-top: 1em;
  margin-bottom: .2em;
}

@media only screen and (max-width: 600px) {
	#contact p {
		font-size: 1.8em
	}
}

@media only screen and (min-width: 787px) and (max-width: 1440px) {
	#contact p {
		font-size: 2.6em
	}
}


